.App{
  background-color: aquamarine;
  height: 100vh;
}

.container{
  background-color: #fff;
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  padding: 20px;
  border-radius: 10px;
  width: 20%;
  height: 20%;
}

input{
  width: 98%;
  outline: none;
  margin: 5px 0 10px 0;
  padding: 5px 0 5px 5px;
}

.soma{
  border-radius: 5px;
  font-size: 30px;
  text-align: center;
  border: 1px solid aquamarine;
  width: 100%;
  padding: 10px 0;
}